.contact-container {
    font-family: Arial, sans-serif;
    padding: 20px;
    max-width: 1321px;
    margin: auto;
    color: #222;
    text-align: left;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.contact-title {
    color: #e6b249;
    margin-bottom: 10px;
    font-family: 'Bely';
}

.contact-list {
    list-style-type: none;
    padding: 0;
    margin-bottom: 20px;
}

.contact-list li {
    margin-bottom: 10px;
}

.contact-list a {
    color: #222;
    text-decoration: none;
}

.contact-list a:hover {
    text-decoration: underline;
}

.contact-divider {
    margin: 20px 0;
    border: none;
    border-top: 1px solid #ccc;
}

.share-journey {
    margin-top: 20px;
}

.share-journey h2{
    color: #222;
    padding-left: 1px;
}

@media(max-width:611px){
    .share-journey h2{
        font-size: 25px;
    }
}