.user-profile-container {
    padding: 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 91%;
    margin: 0 auto;
    text-align: left;
    margin-top: 8rem;
  }
  
  .user-profile-header {
    margin-bottom: 20px;
    color: #2c3e50;
    font-family: 'Noto Sans', sans-serif;
    font-size: 24px;
  }
  
  .user-profile-details {
    font-size: 16px;
    color: #34495e;
  }
  
  .user-profile-details p {
    margin: 5px 0;
    line-height: 1.6;
  }
  