@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
}

.why-participate-container {
  background-color: #f2f2f0; /* Dark black shade */
  color: #222; /* White font color */
  padding: 2rem;
  min-height: 100vh;
  text-align: center;
  padding-top: 4rem;
/*   padding-bottom: 7rem; */
}
.whyparticpiateimg{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 95%;

padding-top: 2rem;
padding-bottom: 2rem;

}
.whyparticpiateimg img{
    width: 611px;
}
.title {
  font-size: 2.5rem;
  text-align: left;
  font-family: 'Bely';
  color: #1D9E91; 
  max-width: 1321px;
}

.benefits {
  list-style: none;
  margin-top: 2rem;
  text-align: left;
  max-width: 1321px;
  margin: 0 auto;
  padding: 0;
  
}

.benefits li {
  margin-bottom: 1.2rem;
  line-height: 1.2;
  font-size: 1.1rem;
  margin-top: 2rem;
}

.benefits strong {
  color: #e6b249; /* Gold for bold text */
  font-weight: 600;
}
.listheadings{
  color: #e6b249; /* Gold for bold text */
  font-weight: 600;
  font-size: 1.5rem;
  margin-bottom: .3rem;
  margin-top: 1rem;
  line-height: 1.1;
}
.listsubhead{
  color: #444; /* Gold for bold text */
  font-weight: 600;
  font-size: .9rem;
  margin-bottom: .5rem;
}
.highlight {
  color: #e6b249; /* Green highlight for profit text */
}

@media(max-width:578px){
  .why-participate-container{
    padding-left: 1.1rem;
    padding-right: 1.1rem;
  }
}