.mysubmissions__container{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    
    padding-top: 3rem;
    width: 100%;
    height: 100%;
}

.mysubmissions__header{
    font-size: 3rem;
    color: var(--text1);
    padding-left: 2rem;

}

.mysubmissions__images{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    padding-left: 2.4rem;
}

.mysubmissions__image{

}
.mysubmissions__image {
    width: 277px; 
    height: 277px;
    overflow: hidden; 
    display: flex; 
    align-items: center; 
    justify-content: center; 
    background-color: var(--bg5);
   /*  box-shadow: var(--shadow2); */
    margin-right: 1rem;
    margin-bottom: 1rem;
    border-radius: 1rem;
    border: 1px solid #c4c4c478;
  }
  
  .mysubmissions__image img {
    width: 100%; /* Make the image responsive */
    height: 100%; /* Ensure it fills the container */
    object-fit: contain; /* Adjust how the image fits */
    border-radius: 8px; /* Optional: Add rounded corners */
    padding: 2rem;
  }
  

  @media (max-width: 400px) {
  
  .mysubmissions__header {
    font-size: 2rem;
    color: var(--text1);
    padding-left: 1.4rem;
  }
   .mysubmissions__images{
    padding-left: 1rem;
   }
   .mysubmissions__images {

    padding-left: 5% !important;
}
  }