.userpanel__container{
    display: flex;
    justify-content: center;
    align-items: center;
     width: 100%;;
    height: 100vh;
    overflow: hidden;
    background-color: #0a0006;
}

.userpanel__innr{
    display: flex;
/*     flex-direction: column; */
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
}
.userpanelitem{
    font-family: 'Poppins';
    font-size: .7rem;
    background-color: #fff;
    color: #0a0006;
    padding: .5rem 1px;
    margin-top: 1rem;
    width: 155px;
    
    border-radius: 7px;
    cursor: pointer;
    text-align: left;
    padding-left: 1rem;
}
.userpanelitem2{
    font-family: 'Poppins';
    font-size: .71rem;
    background-color: #fff;
    color: #0a0006;
    padding: .5rem 1px;
    margin-top: 1rem;
    width: 155px;
    
    border-radius: 7px;
    cursor: pointer;
    position: absolute;
    bottom: 2rem;
}

.userpanel__l{
    width: 271px;
    height: 101%;
    background-color:#1D9E91;
}
.userpanellinks{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.userpanel__r{
    height: 100vh;
    background-color: var(--bg1);
    width: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
}

.userpanel__header{
    font-family: 'Bely';
    font-size: 1.5rem;
    color: #fff;
    margin-top: 4rem;
}




