      padding: 1.9rem; 
      width: 100%;
  height: 100%;
  object-fit: contain;
  padding: 1.9rem;
  border: 1px solid #ccccc5;
  background-color: #fff;
  border-radius: 1rem;
     
    }
    .mysubmissions__image22 p{
         position: absolute;
         left: 11px;
         top: 11px;
         color: #fff;
         background-color: #2f2960;
         border-radius: 1rem;
         width: 31px;
         height: 31px;
         display: flex;
         justify-content: center;
         align-items: center;
         cursor: pointer;
    }
    .mysubmissions__image22 svg{
        position: absolute;
      top: 11px;
      padding: 6px;
      right: 19px;
      width: 33px;
      height: 32px;
      color: #222;
      background-color: burlywood;
      cursor: pointer;
      }
      
    .mysubmissions__image22 button{
        position: absolute;
        right: 11px;
        top: 11px;
        color: #fff;
        background-color: #2f2960;
        padding: .5rem 1rem;

    }


