


.faq-container {
  background-color: #f2f2f0; /* Light grey background */
  color: #222; /* Dark font color */
  padding: 2rem;
  min-height: 100vh;
  text-align: center;
  padding-top: 7rem;

display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
width: 100%;

}
.faqinner{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    max-width: 1100px;
}

.faq-image-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 95%;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.faq-image-container img {
  width: 611px;
}

.faq-title {
  font-size: 2.5rem;
  text-align: left;
  font-family: 'Poppins', sans-serif;
  color: #1D9E91; 
  max-width: 1321px;
/*   width: 99%; */
 display: flex;
 justify-content: flex-start;

}

.faq-list {
  list-style: none;
  margin-top: 2rem;
  text-align: left;
  max-width: 1321px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.faq-item {
  margin-bottom: 1.2rem;
  line-height: 1.2;
  font-size: 1.1rem;
  margin-top: 2rem;
}

.faq-item p:first-of-type {
  color: #e6b249; /* Gold for bold text */
  font-weight: 600;
  margin-bottom: .5rem;
}

.highlight {
  color: #e6b249; /* Green highlight for profit text */
}

@media(max-width:578px){
  .faq-container {
    padding-left: 1.1rem;
    padding-right: 1.1rem;
  }
}
