.mysubmissions__container{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    
    padding-top: 3rem;
    width: 100%;
    height: 100%;
    position: relative;
}

.mysubmissions__header{
    font-size: 3rem;
    color: #fff;
    padding-left: 2rem;

}

.mysubmissions__images{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    padding-left: 2.4rem;
}

.mysubmissions__image{

}
.mysubmissions__image {
    width: 277px; 
    height: 277px;
    overflow: hidden; 
    display: flex; 
    align-items: center; 
    justify-content: center; 
    background-color: #f5f5f5;
    margin-right: 1rem;
    margin-bottom: 1rem;
    cursor: pointer;
  }
  
  .mysubmissions__image img {
    width: 100%; /* Make the image responsive */
    height: 100%; /* Ensure it fills the container */
    object-fit: contain; /* Adjust how the image fits */
    border-radius: 8px; /* Optional: Add rounded corners */
  }
  .showimage{
    width: 90vw;
    height: 90vh;
    background-color: #fff;
    position: fixed;
    left: 5vw;
    top: 5vh;
   
  }
  .showimage img:first-of-type{
    width: 21px;
    position: absolute;
    right: 21px;
    top: 21px;
    cursor: pointer;
  }
 
  .showimagemain{
    width: 610px;
    aspect-ratio: 1;
    margin-left: auto;
    margin-right: auto;
  /*   margin-left: 2rem; */
    margin-top: 1.3rem;
    
  }

  .showimagemain img{
    object-fit: contain !important;
    position: static !important;
    width: 100% !important;
    height: 100% !important;

  }

  .mysubmissions__image2{

  }
  .mysubmissions__image2 {
      width: 277px; 
      height: 277px;
      overflow: hidden; 
      display: flex; 
      align-items: center; 
      justify-content: center; 
      background-color: #f5f5f5;
      margin-right: 1rem;
      margin-bottom: 1rem;
      position: relative;
    }
    
    .mysubmissions__image2 img {
      width: 100%; /* Make the image responsive */
      height: 100%; /* Ensure it fills the container */
      object-fit: cover; /* Adjust how the image fits */
      border-radius: 8px; /* Optional: Add rounded corners */
    }
    .mysubmissions__image2 p{
         position: absolute;
         left: 11px;
         top: 11px;
         color: #fff;
         background-color: #2f2960;
         border-radius: 1rem;
         width: 31px;
         height: 31px;
         display: flex;
         justify-content: center;
         align-items: center;
         cursor: pointer;
    }
    
    .mysubmissions__image2 button{
        position: absolute;
        right: 11px;
        top: 11px;
        color: #fff;
        background-color: #2f2960;
        padding: .5rem 1rem;

    }



    .selectdesigntype {
   
      padding-left: 1rem !important;
     
