@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
}
.guidelinesmain{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f0f6ea;
}

.guidelines-container {
  
  color: #222; /* White text */
  padding: 2rem;
  min-height: 100vh;
  text-align: left;
  max-width: 1311px;
  padding-top: 3rem;
  padding-bottom: 3rem;
  margin-top: 5rem;
}

.guidelinesmain .title {
  text-align: left;
  font-size: 2.5rem;
  font-weight: 600;
  color: #1D9E91;
  margin-bottom: 2rem;
  
 
  
}

.guidelinesmain h2 {
  font-size: 1.8rem;
  color: #e6b249;
  margin-bottom: 1rem;
  padding-left: 0;
  text-align: left;
}

.guidelinesmain h3 {
  font-size: 1.4rem;
  color: #f4f4f4;
  margin-bottom: 0.5rem;
}

.guidelinesmain ul {
  list-style-type: square;
  margin-left: 1.5rem;
  margin-bottom: 1.5rem;
}

.guidelinesmain ul li {
  line-height: 1.6;
  margin-bottom: 0.5rem;
}

.guidelinesmain .category {
  margin-bottom: 2rem;
}

.guidelinesmain .general-guidelines {
  margin-top: 2rem;
  border-top: 1px solid #555;
  padding-top: 1.5rem;
}


