/* Popup overlay style */
.popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent black background */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999; /* Ensure it shows on top */
  }
  
  /* Popup box style */
  .popup {
    background-color: #fff;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    width: 300px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    position: relative;
  }
  .popup img{
    width: 99%;
    
  }
  
  
  .download-btn {
    padding: 10px 20px;
    background-color: #1D9E91;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .download-btn:hover {
    background-color: #45a049;
  }
  
  /* Close button (cross) */

  .buttoncont{
    padding: 1rem;
    position: absolute;
    top: 15px;
    right: 15px;
  }
  .close-btn {
  
    font-size: 1.5rem;
    background: none;
    border: none;
    color: #333;
    cursor: pointer;
    z-index: 221;
    width: 31px;
    height: 31px;
    background-color: #ddd;
    padding: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 2rem;
  }
  
  .close-btn:hover {
    color: #f00;
  }
  