.login__cont{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    min-height: 100vh;
    background-color: #1D9E91;
    font-family: 'Poppins';
    position: relative;
    overflow-y: scroll;
    padding-top: 4rem;
    padding-bottom: 4rem;
    overflow-x: hidden;

}
.mainbg{
  position: absolute;
  right: 1px;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: flex-end;

}
.mainbg img{
  width: 51%;
}

.login__continner{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 371px;
    z-index: 3;
}
.login__header{
  color: #fff;
  font-family: 'Bely';
  font-size: 4rem;
  width: 371px;
  display: flex;
  justify-content: flex-start;
  margin-top: -2rem;
  margin-bottom: 2rem;
}
.form-group {
    margin-bottom: 1.5rem;
    width: 103%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .login__cont label {
    font-size: 0.rem;
    color: #666;
    margin-bottom: 0.4rem;
    margin-left: .4rem;
    display: block;
    display: flex;
    margin-right: auto;
    margin-top: 9px;
  }
  
  .login__cont input ,select{
    width: 100%;
    padding: 0.9rem;
    border: 1px solid #b2b2b2;
    border-radius: 5px;
    font-size: 1rem;
    color: #333;
    background-color: #f9f9f9;
    outline: none;
    transition: border-color 0.3s ease, background-color 0.3s ease;
  }
  .login__cont select{
    margin-bottom: 0rem;
  }
  .login__cont input:focus {
    border-color:#ccfa81 ;
    background-color: white;
  }

  .inputbutton{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #be2e50;
    width: 100%;
    padding: 1rem;
    border: 1px solid #be2e50;
    padding-left: 1rem;
    padding-right: 1rem;
    margin-top: 1rem;
    cursor: pointer;
    color: #f9f9f9;
  }
  
.loginbot{
  font-family: 'Poppins';
  font-size: 1rem;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;

}
.loginbot p{
  color: #be2e50;
  margin: 1rem;
  cursor: pointer;
}
