/* CountdownTimer.css */
.countdown-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
   
    background-color: #f2f2f0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 1rem;
    padding-top: 1rem;
    z-index: 221;
    width: 100%;
  }
  
  .top-text {
    font-size: 1rem;
    
    color: #0d9e92;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-family: 'Poppins';
    margin-bottom: 20px;
    max-width: 611px;
    
  }
  
  .timer {
    display: flex;
    justify-content: center;
    gap: 20px;
    
  }
  
  .time-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fff;
    padding: 1.5rem 1.9rem;
    border-radius: 10px;
    /* box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); */
    color: #333;
    text-align: center;
    background-color: #0d9e92;
    
  }
  
  .time {
    font-size: 3rem;
    font-weight: bold;
    color: #fff;
  }
  
  .label {
    font-size: 1rem;
    font-weight: 600;
    color: #F6f6f6;
  }


  @media (max-width: 800px) {
    .top-text {
      font-size: 1.2rem;
    }
  
    .time {
      font-size: 2rem;
    }
  
    .label {
      font-size: 0.8rem;
    }
    .time-box{
        padding: 1rem 1.4rem;
    }
  }
  
  @media (max-width: 600px) {
    .time-box{
     
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .timer{
        width: 90%;
    }
    .time-box{
        gap: 5%;
        width: 25%;
        aspect-ratio: 1;
    }
    .top-text {
      font-size: 1.2rem;
    }
  
    .time {
      font-size: 1rem;
    }
  
    .label {
      font-size: 0.7rem;
    }
  }
  @media (max-width: 600px) {
    .time-box {
        padding: .3rem .5rem;
    }
  }