@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  background-color: #f2f2f0; /* Dark background */
  color: #ffffff; /* Default white text */
}
.prizesmain{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: left;
    width: 99%;
    padding: 2rem;

}
.judging-container {
 
  min-height: 100vh;
  max-width: 1321px;
  width: 99%;
  margin-top: 4rem;
  padding-top: 4rem;
  margin-bottom: 2rem;
  
}

.criteria-section{
display: flex
;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 0rem;
}
.prizes-content{
    display: flex
;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 0rem;

}

.header h1 {
  text-align: center;
  font-size: 2.5rem;
  color: #1D9E91; /* Pink for main header */
  font-family: 'Bely';
  text-wrap: auto;
    text-align: left;
   
}
.header{
  box-shadow: none !important;
}

.criteria-section,
.prizes-section {
  margin: 1rem 0;
}

.criteria-section h2,
.prizes-section h2 {
  font-size: 2rem;
  color: #e6b249; /* Purple for subheadings */
  margin-bottom: .5rem;
  padding-left: 0% !important;

}

.criteria-content p,
.prizes-content p {
  font-size: 1.1rem;
  line-height: 1.5;
  margin: 0.5rem 0;
  color: #222;
}

.criteria-content span,
.prizes-content .highlight {
  color: #222; /* Pink highlight */
  font-weight: 600;
}

.prizes-content .link {
  color: #b71f1f; /* Optional: Brighter pink for links */
  cursor: pointer;
  text-decoration: underline;
  
}

.prizes-content p {
  margin-bottom: 1rem;
}



