.mysubmissions__container{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    
    padding-top: 3rem;
    width: 100%;
    height: 100%;
}

.mysubmissions__header{
    font-size: 3rem;
    color: #fff;
    padding-left: 2rem;

}

.mysubmissions__images{
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    padding-left: 2.4rem;
}

.mysubmissions__image{

}
.mysubmissions__image {
    width: 277px; 
    height: 277px;
    overflow: hidden; 
    display: flex; 
    align-items: center; 
    justify-content: center; 
    background-color: #f5f5f5;
    margin-right: 1rem;
    margin-bottom: 1rem;
  }
  
  .mysubmissions__image img {
    width: 100%; /* Make the image responsive */
    height: 100%; /* Ensure it fills the container */
    object-fit: contain; /* Adjust how the image fits */
    border-radius: 8px; /* Optional: Add rounded corners */
    padding: 2rem;
  }
  
  .form-group label{
    color: var(--text1);
    text-align: left;
    
    width: 99%;
    margin-left: 3.3rem;
  }


