

.submaincontainer{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
/*   height: 100vh; */
padding-top: 4rem;
padding-bottom: 4rem;
 
}


.upload-container {
   
    width: 100%;
    padding: 20px;
   
   
  }
  
  h2 {
    text-align: center;
    margin-bottom: 20px;
    color: var(--text1);
    
    font-family: 'Poppins';
    display: flex;
    justify-content: flex-start;
    padding-left: 2rem;
    font-size: 4rem;
    font-weight: 400;
  }
  
  .form-groupp {
    margin-bottom: 15px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    
  }
  
  .form-groupp label {
    display: block;
    margin-bottom: 5px;
    color: var(--text1) !important;
    font-weight: 400;
    font-family: 'Poppins';
    padding-left: 2rem !important;
    
  }
  .selectdesigntype{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    padding-left: 2rem;
    width: 100%;
    padding-top: 1rem;
  }
  .selectdesigntype p{
    font-size: 1rem;
    font-weight: 400;
    font-family: 'Poppins';
    background-color: #fff;
    color: #222;
    padding: .5rem 1rem;
    margin-right: 1rem;
    margin-left: .5rem;
    cursor: pointer;
    box-shadow: var(--shadow1);
    border-radius: 11px;
    margin-bottom: 1rem;

  }
  .dropdown, .file-input, .submit-button {
    width: 100%;
    padding: 10px;
    margin-top: 5px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .file-input {
    width: 311px;
    height: 211px;
    display: flex;
   
    justify-content: flex-start;
   
    margin-top: 1rem;
  }
  
  .submit-button {
    background-color: #4caf50;
    color: white;
    cursor: pointer;
  }
  
  .submit-button:hover {
    background-color: #45a049;
  }
  
  .info {
    font-size: 12px;
    color: #777;
    margin-top: 5px;
  }
  
  .preview-container {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    margin-bottom: 15px;
  }
  
  .image-preview img {
    max-width: 70px;
    max-height: 70px;
    border-radius: 5px;
    border: 1px solid #ddd;
  }
  
  .error {
    color: #e74c3c;
    text-align: center;
    margin-bottom: 15px;
  }
  
  .uploadedimage{
    position: relative;
  }
  .imageuploadd{
    margin-right: auto;
  }
  .uploadedimage p{
    position: absolute;
    right: 11px;
    top: 11px;
    z-index: 2;
    font-size: 1rem;
    background-color: #fff;
    padding: .5rem;
    width: 21px;
    height: 21px;
    border-radius: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .sub{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 2.27rem;
  }

  .submit-button{
    width: 211px;
    background-color: #1D9E91;
    border: none;
    font-family: 'Poppins';
    font-size: 1rem;
  }



