/* Wrapper for the entire table */
.custom-table-wrapper {
    width: 100%;
    padding: 20px;
    box-sizing: border-box;
  }
  
  /* Heading for the table */
  .all-users-heading {
    text-align: center;
    margin-bottom: 20px;
    font-size: 24px;
    width: 100%;
    background-color: #2f2960;
    padding-top: 3rem;
    padding-left: 2rem;
    padding-bottom: 3rem;
    color: #fff;
    display: flex;
    justify-content: flex-start;
  }
  
  /* Table container */
  .custom-table {
    display: flex;
    flex-direction: column;
    width: 100%;
   
    border-radius: 8px;
    overflow: hidden;
  }
  
  /* Table header */
  .custom-table-header {
    display: flex;
    background-color: #2f2960;
    font-weight: 511;
    font-family: 'Poppins';
    color: #fff;
    
  }
  
  /* Individual header cell */
  .custom-table-header .custom-table-cell {
    flex: 1;
    padding: 10px;
    text-align: center;
    color: #fff;
  }
  
  /* Table row */
  .custom-table-row {
    display: flex;
    border-bottom: 1px solid #eee;
  }
  
  /* Individual row cell */
  .custom-table-cell {
    flex: 1;
    padding: 10px;
    text-align: center;
    box-sizing: border-box;
    color: var(--text1);
  }
  
  /* Button styling */
  .edit-button, .delete-button {
    padding: 6px 12px;
    margin: 0 5px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .edit-button {
    background-color: #be2e50;
    color: white;
  }
  
  .edit-button:hover {
    background-color: #be2e50;
  }
  
  .delete-button {
    background-color: #f44336;
    color: white;
  }
  
  .delete-button:hover {
    background-color: #e53935;
  }
  
  /* Optional styling for alternating row colors */
  .custom-table-row:nth-child(even) {
    background-color: #f9f9f9;
  }
  
  .custom-table-row:nth-child(odd) {
    background-color: #fff;
  }
  
.page-button{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 31px;
  height: 31px;
  background-color: #f6f6f6;
  color: #333;
  margin-top: 2rem;
  margin-left: 1rem;
  margin-right: 1rem;

}
.active{
  background-color: #2f2960;
  color: #fff;
}
.search-input{
  outline: none;
    background: transparent;
    width: 99%;
    border: 1px solid #77777794;
    height: 41px;
    background-color: #fff;
    margin-top: 1rem;
    margin-bottom: 1rem;
    padding: .4rem;
    border-radius: 5px;
}


  @media (max-width: 451px) {


    .custom-table-cell {
     font-size: 11px;
    }
    .custom-table-wrapper {
   
      padding: 10px;
     
    }
    .all-users-heading {
      text-align: center;
      margin-bottom: 20px;
      font-size: 15px;
      width: 100%;
      background-color: #2f2960;
      padding-top: 1rem;
      padding-left: 2rem;
      padding-bottom: 1rem;
      color: #fff;
      display: flex;
      justify-content: flex-start;
      margin-top: 1rem;
      border-radius: 9px;
    }
  }