.notfound__cont{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    background-color: #e4e2e2;
}


.notfound__cont img{
    width: 190px;
    margin: 2rem 0;
    margin-left: 3rem;
}

.notfound__cont p:first-of-type{
    font-size: 2rem;
    color: #484848;
    margin-bottom: -.5rem;

}

.notfound__cont p:nth-of-type(2){
    font-size: 1rem;
    color: #242424;
}