.login__cont{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    min-height: 100vh;

}


.login__continner{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 371px;
}
.form-group {
    margin-bottom: 1.5rem;
    width: 103%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .login__cont label {
    font-size: 0.rem;
    color: #fff !important;
    margin-bottom: 0.4rem;
    margin-left: .4rem !important;
    display: block;
    display: flex;
    margin-right: auto;
    margin-top: 9px;
    padding: 1px !important;
  }
  
  .login__cont input {
    width: 100%;
    padding: 0.9rem;
    border: 1px solid #b2b2b2;
    border-radius: 5px;
    font-size: 1rem;
    color: #333;
    background-color: #f9f9f9;
    outline: none;
    transition: border-color 0.3s ease, background-color 0.3s ease;
  }
  .error-label{
    margin: 0 !important;
    margin-top: 4px !important;
    font-size: 12px !important;
  }
  .login__cont input:focus {
    border-color: #be2e50;
    background-color: white;
  }

  .inputbutton{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #be2e50;
    width: 100%;
    padding: 1rem;
    border: 1px solid #be2e50;
    padding-left: 1rem;
    padding-right: 1rem;
    margin-top: 1rem;
    cursor: pointer;
    color: #f9f9f9;
  }
  
  
