.dashboard__container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    background-color: var(--bg1);
    height: 100vh;

}


.dashboard__leftpanel{
    width: 241px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    background-color: #2f2960;
    height: 100%;
z-index: 2;
}

.dashboard__header{
    font-family: 'Bely';
    font-size: 1.6rem;
    color: #fff;
    padding: 1rem 1.5rem;
}

.dashboard__header2{
    font-family: 'Poppins';
    font-size: 1rem;
    color: #fff;
    padding: 0rem 1.5rem;
}

.dashboard__links{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-left: 1.5rem;
    margin-top: 4rem;
}

.linkk{
    height: 41px;
    display: flex;
    align-items: center;
    padding-left: 1rem;
    font-size: 1rem;
    background-color: #fff;
    color: #222;
    width: 150px;
    font-size: .8rem;
    border-radius: .3rem;
    cursor: pointer;
    margin-top: .5rem;
    
}
.logoutlink{
    height: 41px;
    display: flex;
    align-items: center;
    padding-left: 1rem;
    font-size: 1rem;
    background-color: #fff;
    color: #222;
    width: 150px;
    font-size: .8rem;
    border-radius: .3rem;
    cursor: pointer;
    position: absolute;
    bottom: 2rem;
}


.dasboard__rpanel{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding-top: 3rem;
    width: 100%;
    height: 100vh;
    overflow-y: scroll;
    overflow-x: hidden;

}

.dashboard__leftpanel2{
    display: none;
   
}
@media(min-width:822px){
   
    .dashboard__leftnav{
    display: none;
      
    } 
 
}

@media(max-width:821px){
    .dashboard__leftpanel{
        display: none;
       
    }
    .dashboard__leftpanel2{
        display: inline;
       
    }
    .dashboard__links{
        margin-left: 0%;
    }
    .dashboard__leftpanel2 {
    
        position: absolute;
        z-index: 32;
        background-color: #2f2960;
        height: 100vh;
    }
    .dashboard__leftnav{
        width: 100vw;
        height: 51px;
        background-color: #2f2960;
        position: absolute;
        top: 0;
        z-index: 121;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        
      
    } 
    .dashboard__leftnav img{
        
        width: 26px;
        cursor: pointer;
        height: 26px;
        margin-left: 1rem;
    }
}