    .submitdesign__sec1{
     
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 99vh;
      background-color: #1D9E92;
      position: relative;
      width: 100%;
      padding-bottom: 2rem;
      padding: 0rem 7rem;

      .submitdesign__left {
       
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        z-index: 21;
    
        p {
          margin: 0;
          color: #333; // Text color
          text-align: left;
        }

      
        p:nth-of-type(1) {
          font-size: 1.5rem; // Large font for welcome text
          color: #fff;
          font-weight: normal;
            margin-left: 2rem;
        }
        .clanfestcss{
          color: #fee687;
          font-weight: bold;
        }
        span{
          color: #fee687;
          font-weight: bold;
        }
        p:nth-of-type(2) {
          font-size: 6rem; // Smaller font for subtitle
          font-weight: 500;
          color: #fff;
          font-family: 'Bely';
          line-height: 6rem;
          margin-left: 1rem;
        }
    
        p:nth-of-type(3) {
          font-size: 7rem; // Very big font
          font-weight: bold;
          color: #9ee5e1; // Accent color for emphasis
          font-family: 'Bely';
          line-height: 6rem;
        }
        p:nth-of-type(4) {
          font-size: 1rem; // Very big font
          font-weight: bold;
          color: #f3f3f3; // Accent color for emphasis
          margin-left: 1rem;
          font-weight: 400;
          margin-top: 1.8rem;
          max-width: 620px;
          
        }
        .submitdeisgn__button {
          margin-top: 2rem;
          cursor: pointer;

          img{
            width: 21px;
            margin-right: .5rem;
          }
    
          button {
            background-color: #fff; // Blue button
            color: #1D9E91; // White text
            border: none;
            padding: 0.8rem 2rem;
            font-size: 1.2rem;
            font-weight: bold;
            border-radius: 5px;
            cursor: pointer;
            transition: background-color 0.3s ease;
            border-radius: 2rem;
            display: flex;
            justify-content: center;
            align-items: center;
           
    
            &:hover {
              background-color: #fff; // Darker blue on hover
            }
          }
        }
      
      }
      .sec1bottomimage{
        position: absolute;
        bottom: 0; 
        transform: translateY(60%); 
        z-index: 221;
        left: 0px;
