.all-designs-container {
    padding: 20px;
   width: 100%;
  }
  
  .all-designs-heading h1 {
    font-size: 24px;
    color: #333;
  }
  
  .all-designs-table-wrapper {
    margin-top: 20px;
    width: 100%;
  }
  
  .custom-table {
    display: block;
    width: 100%;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .table-header,
  .table-row {
    display: flex;
    justify-content: space-between;
    padding: 10px;
  }
  
  .table-cell {
    flex: 1;
    text-align: left;
    padding: 8px;
    font-size: 14px;
    color: #555;
    font-weight: bold;
  }
  
  .table-header {
    background-color: #2f2960;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    color: #fff !important;
    font-family: 'Poppins';

  }
  .tablecellheading{
    color: #fff;
  }
  
  .table-row:nth-child(even) {
    background-color: #fafafa;
  }
  
  .table-row:hover {
    background-color: #e6f7ff;
  }
  
  .design-image {
    max-width: 50px;
    margin-right: 5px;
  }
  .imagecontt {
    width: 45px;
    height: 45px;
    overflow: hidden; 
    margin-right: .5rem;
  }
  
  .imagecontt img {
    width: 100%;
    height: 100%;
    object-fit: cover; 
  }

  .imagemaincont{
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  .actions{
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  .actions img{
   width: 31px;
    padding-right: 5px;
    background-color: #f5f5f5;
    padding: 7px;
    cursor: pointer;
  }