.statspage__cont{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    
    align-items: center;
 
   
    
   
    width: 100%;
    min-height: 99vh;
}


.statuspage__header{
    width: 95%;
    
    
    background-color: #f9f9f9;
    border-radius: .5rem;
    padding-bottom: 2rem;
    
}
.statspage__designsnumber{
    font-size: 2rem;
    font-family: 'Poppins';
    color: #222;
    width: 99%;
    display: flex;
    justify-content: flex-start;
    padding-left: 2rem;
    padding-top: 2rem;
    ;

}
.statspage__usersnumber{
    font-size: 2rem;
    font-family: 'Poppins';
    color: #222;
    width: 99%;
    display: flex;
    justify-content: flex-start;
    padding-left: 2rem;
    padding-top: 2rem;
    ;

}

.designsbycategory__item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;width: 245px;
    background-color: #2f2960;
    color: #fff;
    margin-right: 1rem;
    margin-bottom: 1rem;
}
.designsbycategory{
    display: flex;
    padding-left: 2rem;
    margin-top: 2rem;
    flex-wrap: wrap;
}
.usersbyagegroup__item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;width: 245px;
    background-color: #2f2960;
    color: #fff;
    margin-right: 1rem;
    margin-bottom: 1rem;
}
.usersbyagegroup{
    display: flex;
    padding-left: 2rem;
    margin-top: 2rem;
    flex-wrap: wrap;
}



